.about-page {

    margin-top: 40px;
    height: 85vh;

    h4 {
        margin-top: 20px;
        font-size: 20px;

    }


    h3 {
        margin-top: 30px;
        font-size: 32px;
    }

    .us-container {
        width: 100%;
        justify-content: space-between;

        img {
            width: 240px;
            height: 240px;
            border-radius: 10px;
        }

        h4 {
            font-size: 20px;
            font-weight: bold;
        }

        .kasps {
            width: 33%;
            margin-bottom: 20px;

            .a-fendell {

                width: 240px;
                height: 240px;
                border-radius: 10px;

                background-image: url(../../img/afen.jpg);
                background-size: cover;
            }

            .a-baron {
                width: 240px;
                height: 240px;
                border-radius: 10px;

                background-image: url(../../img/abo.jpg);
                background-size: cover;
            }
        }
    }

    .about-us {
        display: flex;
        flex-direction: column;
        align-items: center;
        z-index: 13;
        width: 100%;

        p {

            margin-top: 20px;
            padding-right: 20px;
            padding-left: 20px;
        }
    }
}


@media (max-width: 480px) {
    .about-page {

        h4,
        p {
            padding-left: 10px;
            padding-right: 10px;
            text-align: center;
        }


        .us-container {
            padding-left: 10px;
            padding-right: 10px;


            .kasps {
                width: 100%;
                .a-baron {
                    height: 150px;
                    width: 150px;
                }
                
                .a-fendell {
                    height: 150px;
                    width: 150px;
                }
            }
        }
    }
}